@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Press Start 2P', cursive;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.App{
  width: 100%;
  height: 100vh;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main{
  width: 450px;
  height: 550px;
  border-radius: 20px;
  background: url('./images/background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}

.mark{
  width: 70px;
  margin: 50px;
}

h1{
  font-size: 22px;
  font-weight: 600;
  text-shadow: 2px 2px black;
}


.num_inp{
  width: 60px;
  height: 60px;
  background-color: transparent;
  border: 3px solid white;
  padding: 5px;
  outline: none;
  color: white;
  font-size: 22px;
  text-shadow: 1px 1px black;
  margin-bottom:20px ;
  text-align: center;

}

.msg{
  text-shadow: 1.5px 1.5px black;
  font-size: 18px;
  margin: 20px;
  height: 50px;
}

.btn_div{
  width: 300px;
  display: flex;
  justify-content: space-between;
}
.btn{
  height: 40px;
  width: 145px;
  border: none;
  box-shadow: 3px 3px black;
  color: white;
  cursor: pointer;
  font-size: 16px;
}

.play_btn{
  background-color: rgb(0, 90, 0);
}

.reset_btn{
  background-color: rgb(0, 12, 213);
}

.lives_main{
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  align-items: center;
  height: 20px;
}

.live_img{
  width: 40px;
}

.lives_count{
  margin: 0 5px ;
  text-shadow: 1.5px 1.5px black;
  font-size: 18px;

}

.right_ans{
  width: 90px;
  /* height: 90px; */
  margin: 50px;
  /* border: 1px solid white; */
  text-shadow: 1.5px 1.5px black;

  text-align: center;
  font-size: 40px;
}




